@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@400;700;900&display=swap);
.form{padding:2rem;text-align:center;width:100vw}.input{padding:0 25px;font:1rem 'Raleway';outline:none;width:350px;height:50px;margin:20px 0px 20px 0px}.button{font:bold 1rem 'Raleway';border:3px solid #AA00FF;background:transparent;color:#AA00FF;outline:none;width:100px;height:50px}.button:hover{background:#AA00FF;color:#eee}

.container{text-align:center;display:grid;grid-template-columns:repeat(auto-fit, minmax(350px, 1fr));justify-items:center}@media screen and (min-width: 499px){.container{padding:0 10px}}@media screen and (min-width: 999px){.container{padding:0 15%}}

.github-profile{margin:20px;float:left;background:darkorchid;box-shadow:3px 15px 30px rgba(171,86,252,0.75);width:325px;height:400px;border-radius:10px;padding:30px 20px 59px 20px;border:0}.profile-pic{height:150px;border-radius:10px}.hr-rule{border-width:1px;border-color:rgba(255,255,255,0.15);margin-top:13px}.name{color:rgba(255,255,255,0.7);font-size:40px;font-weight:900;font-family:'Raleway';letter-spacing:0px;margin-top:20px}.company{color:rgba(255,255,255,0.3);font-size:40px;font-weight:900;font-family:'Raleway';letter-spacing:0px;margin-bottom:20px}

html{font-size:100%}*,*::after,*:before{box-sizing:border-box;margin:0;padding:0}body{background:#FAFAFA}

.base{padding:20px;font-family:'Raleway', sans-sarif;font-size:2rem;font-weight:700;background:#AA00FF;color:#eee;text-align:center;width:100vw}

